import React from 'react';
import Cards from "./components/Cards";
import './App.css';

class App extends React.Component {

  state = {
    langs : []
  }

  componentDidMount() {
    fetch("http://springbootms1-env1.eba-hhmutzsv.us-east-2.elasticbeanstalk.com/languages")
    .then(res => res.json())
    .then(res => this.setState({ langs: res }));
  }

  render() {
    return (
      <div className="App">
        <Cards langs={this.state.langs} />
      </div>
    );
  }
}

export default App;
